html {
  --color-on-hover: #00acdc;
  font-family: Roboto, Helvetica, sans-serif;
}

body {
  font-size: 12px;
}

body, ul, li {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.image-link {
  display: flex;
}

.nav__list {
  padding: 23px 0;
  list-style-type: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  text-align: center;
  color: #000;
  padding: 23px 0;
  text-decoration: none;
}

.nav__link:hover {
  color: var(--color-on-hover);
}

.is-active {
  color: var(--color-on-hover);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color-on-hover);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
